.projects {
  padding: 0 0 50px 0;
}

.projects__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 415px;
  gap: 45px;
}

@media screen and (max-width: 1000px) {
  .projects__grid {
    grid-auto-rows: 360px;
    gap: 40px;
  }
}

@media screen and (max-width: 850px) {
  .projects__grid {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 415px;
  }
}

@media screen and (max-width: 650px) {
  .projects__grid {
    grid-auto-rows: 300px;
    gap: 30px;
  }
}

@media screen and (max-width: 500px) {
  .projects__grid {
    grid-template-columns: 1fr;
    grid-auto-rows: 415px;
  }
}

@media screen and (max-width: 400px) {
  .projects__grid {
    grid-auto-rows: 350px;
  }
}

/* Спиннер */

.projects__spinner {
  display: block;
  position: relative;
  width: 80px;
  height: 80px;
  margin: 100px auto;
}
.projects__spinner div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #647065;
  border-radius: 50%;
  animation: projects__spinner 1.2s linear infinite;
}
.projects__spinner div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.projects__spinner div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.projects__spinner div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.projects__spinner div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.projects__spinner div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.projects__spinner div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.projects__spinner div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.projects__spinner div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.projects__spinner div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.projects__spinner div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.projects__spinner div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.projects__spinner div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes projects__spinner {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
