.menu {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.7s, opacity ease 0.7s;
  z-index: 1;
}

.menu_opened {
  visibility: visible;
  opacity: 1;
}

.menu__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 320px;
  height: 100%;
  position: absolute;
  top: 0;
  right: -320px;
  transition: transform 0.7s;
  z-index: 1;
}

.menu__content_opened {
  transform: translateX(-320px);
}

.menu__close-btn {
  position: absolute;
  background-color: transparent;
  border: none;
  background-position: center;
  background-repeat: no-repeat;
  width: 32px;
  height: 32px;
  top: 40px;
  right: 35px;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 900px) {
  .menu__close-btn {
    top: 30px;
  }
}

@media screen and (max-width: 500px) {
  .menu__close-btn {
    top: 24px;
    right: 28px;
  }
}
