.popup {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  opacity: 0;
  /* transition: visibility 0.5s, opacity ease 0.5s; */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  position: relative;
  width: fit-content;
  height: fit-content;
  background-color: #e5e5e5;
  border-radius: 5px;
  margin: 0;
  box-sizing: border-box;
  overflow: hidden;
}

.popup__close-button {
  position: absolute;
  top: 0px;
  right: 5px;
  border: none;
  background-color: transparent;
  color: #2e3d41;
  padding: 0;
  margin: 0;
  text-indent: 0;
  font-size: 30px;
  line-height: 30px;
  width: fit-content;
  z-index: 1;
}
